@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital@0;1&family=Poppins:wght@600&family=Roboto:wght@300;400&display=swap");

* {
  font-family: "YekanBakh", "Roboto", Arial;
}

body {
  margin: 0;
  font-family: "YekanBakh", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-weight: 500;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.phoneinfo:hover {
  color: black !important;
  transition: color 0.3s ease-in-out;
}
.homeicon:hover {
  color: black !important;
  transition: color 0.3s ease-in-out;
}
.linkitem:hover {
  color: black !important;
  transition: color 0.3s ease-in-out;
}
.brandbox:hover {
  background-color: rgb(245, 245, 247);
}
.buttoncard:hover {
  background-color: rgb(245, 245, 247);
}
.scroll-to-top-button {
  display: block;
  position: fixed;
  border-radius: 0.5rem;
  bottom: 3rem;
  color: white;
  background: rgb(29, 29, 29);
  padding: 0.5rem;
  border: 1px solid rgb(29, 29, 29);
  right: 4rem;
  height: 35px;
  font-size: 2rem;
  display: none;
  cursor: pointer;
  z-index: 1000;
}

.scroll-to-top-button.show {
  display: block;
}
::-webkit-scrollbar-track {
  background-color: #f5f5f5;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(134, 132, 132, 0.605);
}

@media screen and (min-width: 600px) {
  .brandpd {
    padding-left: 2rem;
  }
}
@media screen and (max-width: 600px) {
  .brandpd {
    padding-bottom: 2rem;
  }
}
